@import url('https://fonts.googleapis.com/css2?family=Open+Sans&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-size: 16px;
}

body {
    min-height: 100vh;
    font-family: 'Open Sans', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    display: flex;
    background-color: #efefef;
}

#root {
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
}

.App {
    width: 100%;
    max-width: 1100px;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    border: 1px solid #333;
    box-shadow: 0px 0px 15px gray;
}

.image {
    width: 150px;
    height: 150px;
    display: flex;
    flex-grow: 1;
    justify-content: center;
    margin-left: 35.8vw;
}

.error {
    color: #333;
}

.loader {
    margin-left: 35vw;
    color: #66d8f5;
    animation-name: spin;
    animation-duration: 2500ms;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
}

@keyframes spin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

.Header,
.Footer {
    width: 100%;
    background-color: #66d8f5;
    padding: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.Header h1 {
    font-size: 1.5rem;
}

.Header svg {
    font-size: 2rem;
}

.Footer {
    padding: 0.75rem;
    display: grid;
    place-content: center;
}

.Nav {
    width: 100%;
    background-color: #333;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}

.searchForm {
    width: 80%;
    padding: 1rem 0 0 0.75rem;
}

.searchForm input[type='text'] {
    font-family: 'Open Sans', sans-serif;
    width: 100%;
    min-height: 48px;
    font-size: 1rem;
    padding: 0.25rem;
    border-radius: 0.25rem;
    outline: none;
}

.searchForm label {
    position: absolute;
    left: -99999px;
}

.Nav ul {
    color: #fff;
    list-style-type: none;
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
}

.Nav li {
    padding: 1rem;
}

.Nav li:hover,
.Nav li:focus {
    padding: 1rem;
}

.Nav li a {
    color: #fff;
    text-decoration: none;
}

.Nav li:hover,
.Nav li:focus,
.Nav li:hover a,
.Nav li:focus a {
    background-color: #fff;
    color: #333;
}

.Nav li:hover a,
.Nav li:focus a {
    cursor: pointer;
}

.Home,
.NewPost,
.PostPage,
.About,
.Missing {
    width: 100%;
    flex-grow: 1;
    padding: 1rem;
    overflow-y: auto;
    background-color: #fff;
}

.Missing {
    text-align: center;
}

.post {
    margin-top: 1rem;
    padding-bottom: 1rem;
    border-bottom: 1px solid lightgray;
}

.Home .msg {
    display: flex;
    flex-grow: 1;
    justify-content: center;
    margin-top: 1rem;
}

.err {
    color: #333;
}

.load {
    color: #66d8f5;
}

.Home .post a {
    text-decoration: none;
}

.Home .post a,
.Home .post a:visited {
    color: #000;
}

.post:first-child {
    margin-top: 0;
}

.post:last-child {
    border-bottom: none;
}

.postDate {
    font-size: 0.75rem;
    margin-top: 0.25rem;
}

.postBody {
    margin: 1rem 0;
}

.newPostForm {
    display: flex;
    flex-direction: column;
}

.newPostForm label {
    margin-top: 1rem;
}

.newPostForm input[type='text'],
.newPostForm textarea {
    font-family: 'Open Sans', sans-serif;
    width: 100%;
    min-height: 48px;
    font-size: 1rem;
    padding: 0.25rem;
    border-radius: 0.25rem;
    margin-right: 0.25rem;
    outline: none;
}

.newPostForm textarea {
    height: 100px;
}

.newPostForm button {
    margin-top: 1rem;
    height: 48px;
    min-width: 48px;
    border-radius: 10px;
    padding: 0.5rem;
    font-size: 1rem;
    cursor: pointer;
}

.newPostForm button:focus,
.newPostForm button:hover {
    color: black;
    background-color: limegreen;
    outline: none;
}

.Missing p,
.PostPage p,
.newPost p {
    margin-top: 1rem;
}

.PostPage button {
    height: 48px;
    min-width: 48px;
    border-radius: 0.25rem;
    padding: 0.5rem;
    margin-right: 0.5rem;
    font-size: 1rem;
    color: #fff;
    cursor: pointer;
}
.PostPage button :last-child {
    margin-right: 0;
}

.deleteButton {
    background-color: red;
}

.editButton {
    background-color: #333;
}

@media only screen and (min-width: 610px) {
    html {
        font-size: 22px;
    }

    .Header h1 {
        font-size: 2rem;
    }

    .Nav {
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: space-between;
        align-items: center;
    }

    .Nav ul {
        text-align: right;
    }

    .Nav li:hover,
    .Nav li:focus,
    .Nav li:hover a,
    .Nav li:focus a {
        background-color: #eee;
        color: #333;
    }

    .searchForm {
        width: 50%;
        padding: 0.5rem 0;
    }

    .searchForm input[type='text'] {
        margin-left: 0.5rem;
    }

    .newPostForm textarea {
        height: 300px;
    }
}

@media only screen and (min-width: 992px) {
    .Header svg {
        font-size: 3rem;
    }
}
